import dayjs from "dayjs";
import pubsub from "pubsub-js";
import {getUserBaseInfo, setNewPwd, setBaseInfo} from "@/api/index"
export default {
  data() {
    return {
      //time: "2022-3-14 21:34:18",
      showTopRight: false,
      tip: "欢迎使用！",
      userName: "",
      baseShow: false,
      pwdShow: false,
      disabled1: true,
      disabled2: false,
      phoneNum: "18360725582",
      email: "18360725582@163.com",
      nameCorr: "曹鸿亮",
      oldPWD:"",
      newPWD:"",
      confirmPWD:"",
    };
  },
  props: {
    str: String,
    oldPWD: String,
  },
  mounted() {
    //this.oldPWD = this.pwd;
    //console.log(this.str);
    pubsub.subscribe("sendMsg", (e, msg) => {
      this.showTopRight = msg;
    });
    
  },

  methods: {
    personalConfig() {
      this.text_ = "hello world";
      this.showTopRight = !this.showTopRight;
    },
    async baseInfo() {
      this.tip = "基本资料";
      this.showTopRight = false;
      this.baseShow = true;
      this.pwdShow = false;
      let res = await getUserBaseInfo(this.str)
      console.log(res.data['username'])
      this.userName = res.data['username']
      this.phoneNum = res.data['phone']
      this.nameCorr = res.data['realName']
      this.email = res.data['email']
    },
    exit() {
      this.tip = "退出登录";
      this.showTopRight = false;
      this.baseShow = false;
      this.pwdShow = false;
      this.$router.push({path: '/'});
    },
    async modifyPWD() {
      this.tip = "修改密码";
      this.showTopRight = false;
      this.pwdShow = true;
      this.baseShow = false;
      
    },
    xiugai() {
      this.disabled1 = false;

    },
    queren() {
      this.disabled1 = true;
      console.log(this.phoneNum);
      console.log(this.email);
      console.log(this.nameCorr);
      setBaseInfo(this.str, this.phoneNum, this.email, this.nameCorr)
    },
    querenxiugai() {
      this.disabled2 = true;
      setNewPwd(this.str, this.newPWD);
    }
  },
};
</script>


